<script>
	export default {
		onLaunch: function() {
			this.$store.loginTime = this.date();
			this.$store.onLaunch = 'Y';
			if(uni.getStorageSync("autoLogin")==null){
				uni.setStorageSync("autoLogin",false);
			}
			uni.setStorageSync("debugLevel",this.debugLevel);
		},
		onLoad: function() {
			this.$store.loginTime = this.date();
			console.log('App onLoad');
		},
		onReady: function() {
			console.log('App onReady');
		},
		onShow: function() {
			console.log('App Show');
		},
		onHide: function() {
			if(this.$store.account){
				this.loadJs.saveLoginTime(this.$store.loginTime,this.date());
				this.$store.loginTime = this.date();
			}
			console.log('App Hide');
		},
		onUnload() {
			this.loadJs.saveLoginTime(this.$store.loginTime,this.date());
			console.log('App onUnload');
		},
		destroyed() {
			this.loadJs.saveLoginTime(this.$store.loginTime,this.date());
			console.log('App destroyed');
		}
	}
</script>

<style>
	/* 头条小程序需要把 iconfont 样式放到组件外 */
	@import "components/m-icon/m-icon.css";

	/*每个页面公共css */
	page {
		min-height: 100%;
		display: flex;
		font-size: 16px;
	}

	/* #ifdef MP-BAIDU */
	page {
		width: 100%;
		height: 100%;
		display: block;
	}

	swan-template {
		width: 100%;
		min-height: 100%;
		display: flex;
	}

	/* 原生组件模式下需要注意组件外部样式 */
	custom-component {
		width: 100%;
		min-height: 100%;
		display: flex;
	}

	/* #endif */

	/* #ifdef MP-ALIPAY */
	page {
		min-height: 100vh;
	}

	/* #endif */

	/* 原生组件模式下需要注意组件外部样式 */
	m-input {
		width: 100%;
		/* min-height: 100%; */
		display: flex;
		flex: 1;
	}

	.content {
		display: flex;
		flex: 1;
		flex-direction: column;
		background-color: #efeff4;
		padding: 10px;
	}

	.input-group {
		background-color: #ffffff;
		margin-top: 20px;
		position: relative;
	}

	.input-group::before {
		position: absolute;
		right: 0;
		top: 0;
		left: 0;
		height: 1px;
		content: '';
		-webkit-transform: scaleY(.5);
		transform: scaleY(.5);
		background-color: #c8c7cc;
	}

	.input-group::after {
		position: absolute;
		right: 0;
		bottom: 0;
		left: 0;
		height: 1px;
		content: '';
		-webkit-transform: scaleY(.5);
		transform: scaleY(.5);
		background-color: #c8c7cc;
	}

	.input-row {
		display: flex;
		flex-direction: row;
		position: relative;
		font-size: 18px;
		line-height: 35px;
		padding-left: 10px;
	}

	.input-row .title {
		width: 72px;
		padding-left: 15px;
	}

	.input-row.border::after {
		position: absolute;
		right: 0;
		bottom: 0;
		left: 8px;
		height: 1px;
		content: '';
		-webkit-transform: scaleY(.5);
		transform: scaleY(.5);
		background-color: #c8c7cc;
	}

	.btn-row {
		margin-top: 25px;
		padding: 10px;
	}

	button.primary {
		background-color: #0faeff;
	}
</style>
